import React from "react";
import Slider from "react-slick";
import './Slider.css'
import img from '../Assets/profile.jpeg'
import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";

function Sliders() {

    const settings = {
        dots: true,
        infinite: true,
        speed: 500,
        slidesToShow: 3,
        slidesToScroll: 1
    };
    const navigationItems = [
      { text: 'user 1' },
      { text: 'user 2' },
      { text: 'user 3' },
      { text: 'user 4' },
      { text: 'user 5' },
      { text: 'user 6' },
      { text: 'user 7' }
  ];
  

    return (
        <>
          
            <section className="sider_sec">
            <Container>
                    <h1 className='safety_head'>Testimonials</h1>
                    <Slider {...settings}>
                        {navigationItems.map((item, index) => (
                            <Row className="div_box" key={index}>
                                <Col xl={4}  md={6}  sm={12} className="box_slider">
                                    <Row className="user_div">
                                        <Col lg={2} md={4}>
                                            <img src={img} alt={`user-${index}`} className="img_profile"/>
                                        </Col>
                                        <Col  lg={10} md={8}>
                                            <h5>{item.text}</h5>
                                        </Col>
                                    </Row>
                                    <Row>
                                        <div className="col-12">
                                            <p> Lorem Ipsum has been the industry's standard dummy text ever since
                                                the 1500s, when an unknown printer took a galley of type and scrambled
                                                it to make a type specimen book.</p>
                                        </div>
                                    </Row>
                                </Col>
                            </Row>
                        ))}
                    </Slider>
                    </Container>
            </section>
            
        </>
    )
}


export default Sliders;
