import React from 'react'
import './Ensuring.css'
import img1 from '../Assets/img1.jpg'
import img2 from '../Assets/img2.jpg'
import img3 from '../Assets/img3.jpg'
import img4 from '../Assets/img4.jpg'
import img5 from '../Assets/img5.jpg'
import Container from 'react-bootstrap/Container'
import Row from 'react-bootstrap/Row'
import Col from 'react-bootstrap/esm/Col'


function Ensuring() {
  return (
    <>
        <section className='ensuring_full' id='ensuring'>
            <Container>
                <div className='row head_ensuring'>
                    <h1>Ensuring Safety Of Food From</h1>
                    <h1><span className='f_letter'>Farm</span><span className='s_letter'> to Fork</span></h1>
                </div>
                <Row className='img_con'>
                    <Col xs={12} lg={3} md={5} sm={12}>
                        <img src={img1} alt='img1' className='img_lab_five'/>
                    </Col>
                    <Col xs={9} lg={9} md={7} sm={12} className='paragraph_div'>
                        <p className='paragraph'><h1><span className='f_letter'>F</span>arming</h1>
                            Segregate crop<br/>
                            Store crops safely.<br/>
                            Wash before manufacturing<br/>
                            Take care of work areas</p>
                    </Col>
                </Row>
                <Row className='img_con'>
                    <Col xs={9} lg={9} md={7} sm={12} className='paragraph_div_two'>
                        <p className='paragraph'><h1><span className='f_letter'>M</span>anufacturing</h1>
                        Working with Tools <br/>
                        Maintaining Cleanliness Wear <br/>
                        Safety Equipment (PPE) Keep Work Areas and<br/>
                        Emergency Exits Clear</p>
                    </Col>
                    <Col xs={12} lg={3} md={5} sm={12}>
                    <img src={img2} alt='img2' className='img_lab_five'/>
                    </Col>
                </Row>
                <Row className='img_con'>
                    <Col xs={12} lg={3} md={5} sm={12}>
                     <img src={img3} alt='img3' className='img_lab_five'/>
                    </Col>
                        <Col xs={9} lg={9} md={7} sm={12} className='paragraph_div'>
                            <p className='paragraph'><h1><span className='f_letter'>D</span>istribution</h1>
                                Focus on renewable <br/>
                                materials and packaging<br/>
                                Alternative methods of supply<br/>
                                Transportation safety</p>
                        </Col>
                </Row>
                <Row className='img_con'>
                 <Col xs={6} lg={6} md={4} sm={12}  className='paragraph_div_two'>
                    <p className='paragraph'>
                    Achieving Food Safety in Retail Stores
                     Controlling Package Quality 
                     Maintaining Clean and Hygienic
                    Food Scales
                    Improve customer protection</p>
                    </Col>
                    <Col xs={12} lg={3} md={4} sm={12}className='enso_two_img'>
                    <img src={img4} alt='img4' className='img_lab_five'/>
                    <h1><span className='f_letter'>G</span>roceries</h1>
                    </Col>
                    <Col xs={12} lg={3} md={4} sm={12}className='enso_two_img'>
                    <img src={img5} alt='img4' className='img_lab_five'/>
                    <h1><span className='f_letter'>R</span>estaurant</h1>
                    </Col>
                </Row>
            </Container>
        </section>
    </>

  )
}

export default Ensuring