import React from 'react'
import './Contact.css'
import Container from 'react-bootstrap/Container'
import Row from 'react-bootstrap/esm/Row'
import Col from 'react-bootstrap/esm/Col'

function Contact() {
  return (
    <>
        <section>
            <Container id="contact">
            <h1 className='safety_head'>Contact us</h1>
                <div className='mail_box'>
                    <h2>Email</h2>
                    <input type='email' placeholder='Enter Email' className='email'></input>
                    <h2>Message</h2>
                    <input type='text' placeholder='Enter Text' className='email msg'></input>
                    <div className='send_div'>
                    <button className="getstart_btn send_btn">Send</button>
                    </div>
                    <Row >
                      <Col md={6} sm={12} className='anu' >
                        <button className="getstart_btn send_btn white_btn">Message</button>
                      </Col>
                       <Col md={6} sm={12} className='anu a'>
                         <button className="getstart_btn send_btn white_btn">Get a call back</button>
                        </Col>
                    </Row>
                </div>
            </Container>
        </section>
    </>
  )
}

export default Contact