import React from 'react'
import './Safety.css'
import img from '../Assets/lab_1.jpg'
import img2 from '../Assets/lab_2.jpg'
import img3 from '../Assets/lab_3.jpg'
import Container from 'react-bootstrap/Container'
import Row from 'react-bootstrap/Row'
import Col from 'react-bootstrap/Col'

function Safety() {
  return (
    <>
    <section className='safety_sec' id='service'>
        <Container>
            <Row>
                <h1 className='safety_head'>Food Lab Food Safety Service</h1>
                <Col lg={6} className='transl'>
                    <div className='row'>
                        <div className='col-6'>
                            <img src={img3} className='img_lab2'/>
                            <img src={img2} className='img_lab2'/>
                        </div>
                        <div className='col-6'>
                            <img src={img} className='img_lab3'/>
                        </div>

                    </div>
                </Col>
                <Col lg={6} className=' para_lab_div transR'>
                    <p className='para_lab'>Aperiam quia sapiente vel consequatur consequatur omnis sed autem quae. 
                        Ipsa et minus consequatur vero et doloribus. Ex error illum consequatur amet ea occaecati odit. 
                        Quia similique dolorum officiis distinctio magnam ut facilis. Et dolore est 
                        inventore ea et molestiae earum.</p>
                </Col>
            </Row>
        </Container>
    </section>
    
    </>
  )
}

export default Safety