import React, { Component } from "react";
import Slider from "react-slick";
import img from '../Assets/banner1.png';
import img2 from '../Assets/banner2.jpg';
import img3 from '../Assets/banner3.jpg';
import './Banner.css'


function Banner() {

  const settings = {
    dots: true,
    infinite: true,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1,
  };

  return (
    <>
     <div className="banner" id="banner"> 
        <Slider {...settings}>
          <div className="banner_parent">
          <h3><img src={img} className='img_bg'/>
          <div className="banner_child fis_para">
          <span className="banner_b_para ">Empowering Society<br/>Through Science</span><br/>
          <span className="banner_s_para">Food Lab is one point solution for all<br/> food safety needs</span><br/>
          <button className="getstart_btn">Get Started</button>
          </div>
          </h3>
          </div>
          <div className="banner_parent">
          <h3><img src={img2} className='img_bg'/>
          <div className="banner_child">
          <span className="banner_b_para">Agriculture encompasses crop and<br/> livestock production</span><br/>
          <span className="banner_s_para">Food Lab is one point solution for all<br/> food safety needs</span><br/>
          <button className="getstart_btn">Get Started</button>
          </div>
          </h3>
          </div>
          <div className="banner_parent">
          <h3><img src={img3} className='img_bg'/>
          <div className="banner_child">
          <span className="banner_b_para">Agriculture is one of the bases<br/> of Argentina's economy.</span><br/>
          <span className="banner_s_para">Food Lab is one point solution for all<br/> food safety needs</span><br/>
          <button className="getstart_btn">Get Started</button>
          </div>
          </h3>
          </div>
        </Slider>
      </div>
    </>
  );
}

export default Banner;
