import React from 'react';
import './Header.css';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Container from 'react-bootstrap/Container';
import Nav from 'react-bootstrap/Nav';
import Navbar from 'react-bootstrap/Navbar';
import Offcanvas from 'react-bootstrap/Offcanvas';

const navigationItems = [
  { text: 'Home', link: '#banner' },
  { text: 'Service', link: '#service' },
  { text: 'Gallery', link: '#ensuring' },
  { text: 'Co-Traveller' },
  { text: 'FAQs' },
  { text: 'contactUs', link: '#contact' },
  { text: 'Register' }
];

function Header() {
  return (
    <section className='HeaderSec'>
      <Container>
        <Row className='header_div'>
          <Col  xs={3} md={4} lg={3} sm={5}>
            <h1 className='logo'>Food Lab</h1>
          </Col>
          <div className='col-8 col-lg d-none d-lg-block nav_sec'>
            <ul className='navui'>
              {navigationItems.map((item, index) => (
                <li className='nave' key={index}>
                  {item.link ? <a href={item.link}>{item.text}</a> : item.text}
                </li>
              ))}
            </ul>
           
          </div>
          <Col  xs={9} md={8} lg={1} sm={7}>
        {[false].map((expand) => (
              <Navbar key={expand} expand={expand} className="end mb-3">
                
                  <Navbar.Toggle aria-controls={`offcanvasNavbar-expand-${expand}`}  />
                  <Navbar.Offcanvas
                    id={`offcanvasNavbar-expand-${expand}`}
                    aria-labelledby={`offcanvasNavbarLabel-expand-${expand}`}
                    placement="end"
                  >
                    <Offcanvas.Header closeButton>
                      <Offcanvas.Title id={`offcanvasNavbarLabel-expand-${expand}`}>
                        Food Lab
                      </Offcanvas.Title>
                    </Offcanvas.Header>
                    <Offcanvas.Body>
                      <Nav className="justify-content-end flex-grow-1 pe-3">
                        {navigationItems.map((item, index) => (
                          <Nav.Link key={index} href={item.link} className='nave'>
                            {item.text}
                          </Nav.Link>
                        ))}
                      </Nav>
                    </Offcanvas.Body>
                  </Navbar.Offcanvas>
               
              </Navbar>
            ))}
            </Col>
        </Row>
       
      </Container>
    </section>
  );
}

export default Header;
